import { useEffect, useState } from "react";
import data from "./data.json"
import { Chart as ChartJS, ArcElement, CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend, Filler } from "chart.js";
import { Line } from 'react-chartjs-2';
import './App.css';

ChartJS.register(
  ArcElement,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  Filler
);

function App() {

  const mapiurl="https://654c9e5a77200d6ba8591a8f.mockapi.io/datos";
  async function sendRequest(url, method, dat = null){
    const requestOptions = {
      method,
      headers: {
        'Content-Type': 'application/json',
      },
      body: dat ? JSON.stringify(dat) : null,
    };
  
    try {
      const response = await fetch(url, requestOptions);
  
      if (!response.ok) {
        throw new Error(`Request failed with status: ${response.status}`);
      }
  
      const responseData = await response.json();
      return responseData;
    } catch (error) {
      throw error;
    }
  }

  const [chartData, setChartData] = useState({})
  const [datao, setDataOnline] = useState([])
 

  useEffect(() => {

    sendRequest(mapiurl, 'GET').then((response) => {setDataOnline(response); console.log(response)});

  }, [])

useEffect(() => {
  setChartData({
    labels: datao.map(item => item.seconds),
    datasets: [
      {
        label: 'Temperatura medida',
        data: datao.map((item) => item.temperatura),
        fill: true,
        borderColor: 'rgb(255, 99, 132)',
        backgroundColor: 'rgba(255, 99, 132, 0.5)',
      },
    ],
  });
}, [datao])

const anadirdat = ()=>{
  const postData = { temperatura: document.getElementById("temp").value, seconds: document.getElementById("sec").value };
  console.log(postData)
  if(postData.temperatura!=""){
  sendRequest(mapiurl, 'POST', postData)
    .then((data) => {
      console.log('Solicitud POST exitosa', data);
    })
    .catch((error) => {
      console.error('Error en la solicitud POST', error);
    }).finally(()=>{refresh()});
  }
}

function refresh(){
  sendRequest(mapiurl, 'GET').then((response) => {setDataOnline(response); console.log(response)});
}

  return (
    <div className="App">
      <div className='chart'>
        {
          chartData && chartData?.datasets && (
            <Line 
              options={ {
                  responsive: true,
                  plugins: {
                    legend: {
                      position: 'top',
                    },
                    title: {
                      display: true,
                      text: 'Lab6',
                    },
                  },
                }} 
              data={chartData} 
            />
          )
        }
      </div>
      <div className="input-group my-3 w-50">
      <input id="temp" type="number" className="form-control" placeholder="Temperatura"/>
        <input id="sec" type="number" className="form-control" placeholder="Segundos"/>
        <button onClick={anadirdat} className="btn btn-success">ENVIAR A LA BD</button>
      </div>
      <p>Si desea ver la base de datos en la nube visite: <a href={mapiurl}>datos</a></p>
    </div>
    
  );
}

export default App;
